import React, {useState} from 'react';
import './App.css';
import { Body } from './Body';

export class Header extends React.Component {
    render() {
    let resume = 'https://drive.google.com/file/d/1jE4lTVO7tJdLdDF_1sxOMB99HZ2zA5sY/view?usp=sharing'
    let whiteMain = 'rgb(255,255,255)'
    let whiteHeader = 'rgb(250,250,250)'
    let greyLetter = 'rgb(169,169,178)'
    let blackMain = 'rgb(41,42,45)';
    let blackHeader = 'rgb(37, 38, 39)'

    // where 1 is equal to white mode
    let color = 1;

    let identifier = (id) => {
        return document.getElementById(id);
    }
    

    const swapMode = () => {;
        if (color) {
            identifier('one').style.backgroundColor = blackHeader;
            identifier('two').style.backgroundColor = blackHeader;
            identifier('my').style.color = greyLetter;
            identifier('project').style.color = greyLetter;
            identifier('resume').style.color = greyLetter;
            identifier('icon').style.color = greyLetter;
            identifier('one').style.color = greyLetter;
            identifier('two').style.color = greyLetter;
            identifier('content').style.backgroundColor = blackMain;
            identifier('p1').style.backgroundColor = blackMain;
            identifier('p2').style.backgroundColor = blackMain;
            identifier('p3').style.backgroundColor = blackMain;
            identifier('p4').style.backgroundColor = blackMain;
            color = 0;
        } else {
            identifier('one').style.backgroundColor = whiteHeader;
            identifier('two').style.backgroundColor = whiteHeader;
            identifier('my').style.color = 'black';
            identifier('project').style.color = 'black';
            identifier('resume').style.color = 'black';
            identifier('icon').style.color = 'black';
            identifier('one').style.color = 'black';
            identifier('two').style.color = 'black';
            identifier('content').style.backgroundColor = whiteMain;
            identifier('p1').style.backgroundColor = whiteMain;
            identifier('p2').style.backgroundColor = whiteMain;
            identifier('p3').style.backgroundColor = whiteMain;
            identifier('p4').style.backgroundColor = whiteMain;
            color = 1;
        }

    }
       return ( 
       <div className='header' id='one'> 
             <div className='inner_header' id='two'>
                 <div className='name'>
                 <h1 style={{fontSize: 20}} id='my'> pisun chen </h1>
                </div>  
       <ul className='nav'>
           <div className='div_ref' id='three'>
               <li ><a id='project'>Projects </a></li>
               <li ><a href={resume} id='resume' target={'_blank'}>Resume</a></li>
               <li>|</li>
               <button onClick={swapMode}> <li id='icon' style={{fontSize: 30}}>â—‘</li> </button>
           </div>
       </ul> 
       </div>
       <Body/>

       </div> 
       )
    }
}
